.benefit {
  display: flex;
  justify-content: space-between;
  width: 940px;

  @media (max-width: 320px) {
    width: 100%;
    flex-direction: column;
    gap: 20px;
  }

  &__left,
  &__right-top {
    width: 460px;
    border-radius: 24px;
    background-color: #0e1e31;
    padding: 60px;

    @media (max-width: 320px) {
      width: 100%;
      padding: 25px;
    }
  }

  &__title {
    font-size: 48px;
    font-weight: 900;
    color: $c-aqua;
  
    @media (max-width: 320px) {
      font-size: 24px;
    }
  }
  
  &__text {
    font-size: 18px;
    line-height: 24px;
    color: white;
    margin-top: 20px;
  
    @media (max-width: 320px) {
      font-size: 14px;
    }
  }
  
  &__btn {
    margin-top: 20px;
  }
  
  &__right {
    width: 460px;
  
    @media (max-width: 320px) {
      width: 100%;
    }
  }
  
  &__right-bottom {
    display: flex;
    justify-content: space-around;
    height: 64px;
    font-size: 18px;
    font-weight: 500;
    line-height: 64px;
    color: white;
    background-color: $c-red;
    box-shadow: 0 10px 20px rgba($c-red, 0.3);
    border-radius: 24px;
    padding: 0 24px;
    margin-top: 20px;
  
    @media (max-width: 320px) {
      height: 32px;
      font-size: 12px;
      font-weight: 500;
      line-height: 32px;
      border-radius: 10px;
      padding: 0 10px;
    }
  }
  
  &__right-bottom-img {
    width: 40px;
    height: 40px;
    transform: translateY(calc(50% - 8px));
  
    @media (max-width: 320px) {
      width: 28px;
      height: 28px;
      transform: translateY(2px);
    }
  }
}
